م لالجین و خدشه دار کردن اعتبار مهمترین و قدیمی ترین مرکز\n              تولید سفال خاورمیانه بود.\n              شهر سفال یک وبسایت آموزشی-فروشگاهی است که از تیرماه سال ۱۳۹۳ با تلاش جمعی از تولیدکنندگان شناخته سفال و\n              سرامیک و با هدف حفظ شکوه نام لالجین کار خود را آغاز کرده است.\n              هدفی که رسیدن به آن مستلزم نیازسنجی در بازارهای هدف و طراحی و تولید محصولات با کیفیت (چه بصورت تولید\n              مستقیم و چه به صورت نظارت دقیق بر فرآیند تولید دیگر واحدها) مطابق با نیاز روز مشتری و عبور از موانع\n              موجود در مسیر خرید رضایت بخش مشتریان عمده و خرده می باشد.\n              شهر سفال اکنون\n              ▫️به فروشندگان سفال و سرامیک در سراسر کشور، گلچینی از بهترین تولیدات خود و همکاران (با ضمانت اصالت و\n              کیفیت)\n              ▫️به واحدهای تولیدی مستقر در شهرهای دیگر، مواد اولیه (اعم از انواع گِل، دوغاب، استین، لعاب، وسایل مبلمان\n              کوره و… )\n              ▫️و به علاقمندان این حوزه، در کنار فروش، آموزش نحوه تشخیص انواع سفال و سرامیک با کیفیت از نوع بی کیفیت و\n              تقلبی آن و نحوه صحیح نگهداری از آن ها را ارائه می دهد.")]),_vm._v(" "),_c('p',[_vm._v("مهمترین دغدغه ما حفظ اصالت شهر جهانی سفال و آسان سازی خرید مطمئن از لالجین در کمترین زمان و با مناسب\n              ترین قیمت می باشد.")]),_vm._v(" "),_c('p',[_vm._v("برای تداوم حیات کارگاه های سنتی سفالگری و کارخانجات مدرن سرامیک سازی و نیز تداوم لبخند رضایت ناشی از\n              خرید موفق شما، ما همواره خواهیم کوشید.")])]),_vm._v(" "),_c('div',{staticClass:"about-us__body-services-us"},[_c('div',{staticClass:"about-us__body-services-us-title"},[_c('h2',[_vm._v("خدمات ما")]),_vm._v(" "),_c('div',{staticClass:"about-us__body-services-us-text"},[_c('p',[_vm._v("مهمترین دغدغه ما حفظ اصالت شهر جهانی سفال و آسان سازی خرید مطمئن از لالجین در کمترین زمان و با\n                  مناسب\n                  ترین قیمت می باشد.")])])]),_vm._v(" "),_c('div',{staticClass:"about-us__body-services-us-items"},[_c('v-row',[_c('v-col',{attrs:{"cols":"12","lg":"4","md":"4",